<template>
  <div class="main">
    <div class="crumb_box">
      <div class="search_box">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item>首页</el-breadcrumb-item>
          <el-breadcrumb-item>登入日志</el-breadcrumb-item>
        </el-breadcrumb></div>
    </div>
    <div class="clear"></div>
    <div class="application_item" style="height:calc(100vh - 198px);">
      <div class="btn_box">
        <div class="common-transverse-operation">

          <!-- 时间选择器 -->
          <!--  type="datetimerange"
          @focus="focus"-->
          <div class="search-date">
            <el-date-picker
              @change="getTimeDate"
              v-model="table.search.date"
              clearable
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期" end-placeholder="结束日期"
            ></el-date-picker>
          </div>

          <!-- 带搜索功能的input -->
          <!-- <div class="search-input" style="float: left;margin-left:70px">
            <el-autocomplete
              placeholder="搜索姓名"
              v-model="table.search.keywordOfName"
              :fetch-suggestions="querySearchAsync"
              @select="handleSelect"
            ></el-autocomplete>
          </div> -->

          <!-- 关键字搜索 -->
          <!-- clearable -->
          <div class="search-input">
            <el-input
              placeholder="用户名称、所属机构、设备类型、ip/系统"
              suffix-icon="el-icon-search"
              v-model="table.search.keyword"
              @change="getLogDataList(1)"
            ></el-input>
          </div>

        </div>

        <div class="clear"></div>
        <div class="table_item">
          <el-table ref="multipleTable" stripe :data="allTableData" tooltip-effect="dark" height="calc(100vh - 245px)" style="width: 100%">
            <el-table-column prop="logId" label="日志编号" width="80"></el-table-column>
            <el-table-column prop="userId" label="用户代码" width="120"></el-table-column>
            <el-table-column prop="userName" label="用户名称" width="80"></el-table-column>
            <el-table-column prop="orgName" label="所属机构" width="120"></el-table-column>
            <el-table-column prop="deviceType" label="设备类型"></el-table-column>
            <el-table-column prop="loginVersion" label="IP地址或系统"></el-table-column>
            <el-table-column prop="loginDevice" label="MAC地址或机型"></el-table-column>
            <el-table-column label="登录时间" width="165">
              <template slot-scope="scope">
                <span v-text="dateFrm(scope.row.loginTime)"></span>
              </template>
            </el-table-column>
            <el-table-column label="退出时间" width="165">
              <template slot-scope="scope">
                <span v-text="dateFrm(scope.row.exitTime)"></span>
              </template>
            </el-table-column>
            <el-table-column prop="domainId" label="在线时长"></el-table-column>
          </el-table>
        </div>

        <div class="pagination">
          <el-pagination
            :page-sizes="[10, 20, 50, 100]"
            :page-size="pagination.sizes"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pagination.total"
            :current-page='pagination.page'
            @current-change='handleCurrentChange'
            @size-change='handleSizeChange'>
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {getLoginLog} from '@/api/UserApi';
  import moment from 'moment'

  export default {
    data() {
      return {
        allTableData: [],
        filterTableData: [], //搜索框检索数据
        pagination: {
          page: 1,
          sizes: 20,
          total: 0,
        },
        table: {
          search: {
            date: [],
            keyword: '',
            keywordOfName: '',
            startTime: '',
            endTime: '',
            timeout: null,
            dataList: [],
            userNameList: []
          },
          dataList: []
        }
      }
    },
    mounted() {
      this.getLogDataList()
    },

    methods: {

      dateFormat: (row, column) => {
        const date = row[column.property];
        if (date === undefined) {
          return ''
        }
        return moment(date).format('YYYY-MM-DD HH:ss:mm') //这里的格式根据需求修改
      },
      dateFrm: (e) => {
        // console.log(e);
        return moment(e).format('YYYY-MM-DD HH:ss:mm');
      },

      //【系统日志】获取日志列表数据
      getLogDataList(type) {
        if (type === 1) { //如果type=1时,表示为关键字搜索,设置page=1,检索后的数据从第一页开始浏览
          this.pagination.page = 1;
        }

        getLoginLog({
          page: this.pagination.page,
          size: this.pagination.sizes,
          keyword: this.table.search.keywordOfName ? this.table.search.keywordOfName : this.table.search.keyword,
          start: this.table.search.startTime,
          end: this.table.search.endTime
        }).then(res => {
          if (res.data.code === this.$global.successCode) {
            this.allTableData = res.data.data.list;
            this.pagination.total = res.data.data.total;
            this.filterTableData = this.allTableData;
          } else {
            this.$message({
              message: '数据请求失败!',
              type: 'error'
            })
          }
        });

      },

      //【系统日志】分页-设置日志列表的page和size
      handleCurrentChange(page) {
        this.pagination.page = page;
        this.getLogDataList();
      },
      handleSizeChange(sizes) {
        this.pagination.sizes = sizes;
        this.getLogDataList();
      },

      getTimeDate() {
        if (this.table.search.date) {
          this.table.search.date.forEach((item, i) => {
            this.table.dataList[i] = moment(item).valueOf()
          });
          this.table.search.startTime = this.table.dataList[0];
          this.table.search.endTime = this.table.dataList[1];
          this.getLogDataList(1);
        } else {
          this.table.search.startTime = "";
          this.table.search.endTime = "";
          this.getLogDataList(1);
        }
      },
    }
  };
</script>

<style scoped>
  .search-date {
    width: 350px;
    float: left;
  }

  .search-type {
    width: 80px;
    float: left;
    margin-left: 10px;
  }

  .search-button {
    width: 120px;
    float: left;
    margin-left: 60px;
  }

  .search-input {
    width: 280px;
    float: right;
  }
</style>



